<template>
  <div 
      class="folder-wrapper"
      @click="click = !click"
      :class="folders && files ? 'active-folder' : ''"
  >
    <div class="image-wrapper">
      <img src="../assets/folder.svg" alt="" class="folder-img">
    </div>
    <p class="folder"> {{name}} </p>
  </div>
  <template v-for="folder in folders">
    <div v-if="folders && files && click" class="inserted-folder">
      <FolderView :name="folder.name" :folders="folder.folders" :files="folder.files"/>
    </div>
  </template>
  <template v-for="file in files">
    <div v-if="folders && files && click" class="inserted-folder">
      <FileView :name="file.name"/>
    </div>
  </template>
</template>

<script>
import FileView from "./FileView";
export default {
  name: 'FolderView',
  components: {FileView},
  props: {
    name: String,
    folders:Array,
    files:Array
  },
  data: () => {
    return{
      click: false
    }
  }
}
</script>

